import React, { useState, useEffect } from 'react';
import { Button, Form, Container, Header } from 'semantic-ui-react';
import { Link } from 'gatsby';
import axios from 'axios';
import "../css/evalForm.css";
import { stripBasename } from 'history/PathUtils';
import { createBrowserHistory } from "history";

function evalForm() {

    // const [SchEvaluation_id, setSchEval] = useState('');
    // const [StudentWriter_id, setStudentWriter] = useState('');

    const [DisciplinaryKnowledge_value, setKnowledge] = useState('');
    const [IntellectualCreativeSkills_value, setCreative] = useState('');
    const [InterpersonalSkills_value, setInter] = useState('');
    const [GlobalCitizenship_value, setCitizen] = useState('');
    const [PersonalMastery_value, setMastery] = useState('');
    const [Contributions_value, setContributions] = useState('');
    const [Teamcommunicationskill_value, setTeamwork] = useState('');
    const [Contributionpercent_value, setPercent] = useState('');
    const [Improvement_value, setImprove] = useState('');
    const [StudentReciever_id, setStudentReceiver] = useState('');

    const username = localStorage.getItem('username');
    const usernameString = <p> {username} </p>

    localStorage.setItem('StudentReceiver_id', StudentReciever_id);

    const history = createBrowserHistory();

    const directToSuccess = () => {
        history.push('/success');
        window.location.reload();
    };

    const handleSubmit = (e) => {
        // e.preventDefault();
        console.log("submit");
        console.log(username);

        // SchEvaluation_id, StudentWriter_id, 
        const obj = { DisciplinaryKnowledge_value, IntellectualCreativeSkills_value, InterpersonalSkills_value, GlobalCitizenship_value, PersonalMastery_value,
                        Contributions_value, Teamcommunicationskill_value, Contributionpercent_value, Improvement_value, StudentReciever_id };
        console.log(obj);

        axios            
            .post(
                'https://sheet.best/api/sheets/20e852df-7a03-4f00-80f1-acc57b011038',
                obj
            )
            .then((response) => {
                console.log(response);
            });

        directToSuccess();
    };

    return (
        <Container fluid className="container">
            <Header as="h2">Peer Evaluation For: </Header>
            {/* Implement drop down box with names. */}
            <select name="StudentReciever_id" id="StudentReciever_id" onChange={(e) => setStudentReceiver(e.target.value)}>
                <option value="default" selected="true">Choose one name.</option>
                <option value="4">Jacob Mims</option>
                <option value="1">Pavan Sastri</option>
                <option value="2">Kirk Graham</option>
                <option value="9">Jacquelyn</option>
                <option value="3">Allie Ahwee-Marrah</option>
                <option value="5">Derek Stoughton</option>
                <option value="6">Natalie Calizon</option>
                <option value="7">Vignesh Sinha</option>
            </select>
            <div></div><br />

            <Form className="form">
                <Form.Field>
                    <label class="formlabels">Peer Evaluation Completed by: <p id="user">{usernameString}</p></label>
                </Form.Field>

                <Form.Field>
                    <bold>1. What grade out of 100 do you think this individual should receive based on their efforts with the group project?</bold><br />
                    <label class="formlabels">Disciplinary + Multidisciplinary Knowledge  </label>
                    <input type="text" id="DisciplinaryKnowledge_value" name="DisciplinaryKnowledge_value" size="3" onChange={(e) => setKnowledge(e.target.value)}></input><br />
                    <label class="formlabels">Intellectual + Creative Skills  </label>
                    <input type="text" id="IntellectualCreateiveSkills_value" name="IntellectualCreateiveSkills_value" size="3" onChange={(e) => setCreative(e.target.value)}></input><br />
                    <label class="formlabels">Interpersonal Skills  </label>
                    <input type="text" id="InterpersonalSkills_value" name="InterpersonalSkills_value" size="3" onChange={(e) => setInter(e.target.value)}></input><br />
                    <label class="formlabels">Global Citizenship  </label>
                    <input type="text" id="GlobalCitizenship_value" name="GlobalCitizenship_value" size="3" onChange={(e) => setCitizen(e.target.value)}></input><br />
                    <label class="formlabels">Personal Mastery  </label>
                    <input type="text" id="PersonalMaster_value" name="PersonalMaster_value" size="3" onChange={(e) => setMastery(e.target.value)}></input>
                    <div></div><br />
                </Form.Field>

                <Form.Field>
                    <bold>2. Please describe the contributions of this team member below.</bold><br />
                    <textarea id="Contributions_value" name="Contributions_value" rows="10" cols="50" onChange={(e) => setContributions(e.target.value)}></textarea>
                    <div></div><br />
                </Form.Field>

                <Form.Field>
                    <bold>3. Please select the option that best aligns to your analysis of the member's teamwork and communication skills.</bold>
                    <div class="radio">
                        <input type="radio" id="choice1" name="Teamcommunicationskill_value" value="terrible" onChange={(e) => setTeamwork(e.target.value)}></input>
                        <label for="choice1">Terrible</label>
                        <input type="radio" id="choice2" name="Teamcommunicationskill_value" value="subpar" onChange={(e) => setTeamwork(e.target.value)}></input>
                        <label for="choice2">Sub-Par</label>
                        <input type="radio" id="choice3" name="Teamcommunicationskill_value" value="average" onChange={(e) => setTeamwork(e.target.value)}></input>
                        <label for="choice3">Average</label>
                        <input type="radio" id="choice4" name="Teamcommunicationskill_value" value="good" onChange={(e) => setTeamwork(e.target.value)}></input>
                        <label for="choice4">Good</label>
                        <input type="radio" id="choice5" name="Teamcommunicationskill_value" value="excellent" onChange={(e) => setTeamwork(e.target.value)}></input>
                        <label for="choice5">Excellent</label>
                    </div>
                    <div></div><br />
                </Form.Field>

                <Form.Field>
                    <bold>4. What % contribution did this member have towards the project?</bold><br />
                    <input type="text" id="Contributionpercent_value" name="Contributionpercent_value" size="3" onChange={(e) => setPercent(e.target.value)}></input>%
                    <div></div><br />
                </Form.Field>

                <Form.Field>
                    <bold>5. What could this member do to improve?</bold><br />
                    <textarea id="Improvement_value" name="Improvement_value" rows="5" cols="50" onChange={(e) => setImprove(e.target.value)}></textarea>
                    <div></div><br/>
                </Form.Field>

                <Button color="blue" type='submit' onClick={handleSubmit}>Submit</Button>
            </Form>
        </Container>
    );
}

export default evalForm;